<template>
  <div class="nong-recharge">
    <NavBar />
    <div class="nong-recharge--title">充值</div>
    <van-row class="nong-recharge--card theme-card">
      <van-col span="20" class="card-account">
        <div class="account-info">
          <div class="info-name theme-account-name">{{ payAccountName }}</div>
          <div class="info-number theme-account-merchantNo">{{ merchantNo }}</div>
        </div>
      </van-col>
      <van-col span="4">
        <div class="account-logo"></div>
      </van-col>
    </van-row>
    <div class="nong-recharge--input-field">
      <div class="input-label">充&nbsp;值&nbsp;金&nbsp;额</div>
      <van-field
        class="money-input theme-hr-color"
        readonly
        clickable
        label-class="input-label-class"
        :value="orderAmount"
        label="￥"
        :border="false"
        @touchstart.native.stop="isShow = true"
      />
    </div>
    <van-goods-action
      safe-area-inset-bottom
      class="transition-drop-down"
      :class="{ 'drop-down': isShow }"
    >
      <van-button type="warning" class="btn recharge theme-btn-bg" @click="go2Next"
        >下一步</van-button
      >
    </van-goods-action>
    <van-number-keyboard
      safe-area-inset-bottom
      :hide-on-click-outside="false"
      theme="custom"
      extra-key="."
      class="nong-recharge--keyboard theme-keyboard-color"
      close-button-text="确定"
      :show="isShow"
      v-model="orderAmount"
      maxlength="9"
      @blur="isShow = false"
    >
      <div slot="delete">删除</div>
    </van-number-keyboard>
  </div>
</template>

<script>
import NavBar from '../navbar'
import { Button, Row, Col, Field, GoodsAction, NumberKeyboard, Toast } from 'vant'
export default {
  name: 'recharge',
  components: {
    [Button.name]: Button,
    [Row.name]: Row,
    [Col.name]: Col,
    [Field.name]: Field,
    [NumberKeyboard.name]: NumberKeyboard,
    [GoodsAction.name]: GoodsAction,
    [Toast.name]: Toast,
    NavBar
  },
  data() {
    return {
      orderAmount: '',
      isShow: true,
      payAccountName: decodeURIComponent(this.$route.query.name) || '',
      merchantNo: this.$route.query.merchantNo || '',
      accountCode: this.$route.params.anCode || ''
    }
  },
  methods: {
    go2Next() {
      // 下一步
      //   if (this.orderAmount === '') {
      //     Toast.setDefaultOptions({ duration: 500 })
      //     Toast.fail('请输入充值金额')
      //     return false
      //   } else {
      //     this.$router.push({
      //       path: `/payment/${this.accountCode}`,
      //       query: {
      //         orderAmount: this.orderAmount
      //       }
      //     })
      //   }
      Toast.setDefaultOptions({ duration: 500 })
      Toast('为了演示方便，去掉了校验')
      this.$router.push({
        path: `/payment/${this.accountCode}`,
        query: {
          orderAmount: this.orderAmount
        }
      })
    }
  }
}
</script>

<style lang="less">
.nong-recharge {
  width: 100%;
  color: @ft-white;
  font-size: @base-size * 1.4;
  box-sizing: border-box;
  background: url(../../assets/images/ssg/green_withdraw.png) no-repeat top;
  background-size: contain;
  &--title {
    font-size: @base-size * 2.7;
    padding: 0 @base-size*2.6 @base-size*2 @base-size*2.6;
    box-sizing: inherit;
  }
  &--card {
    width: 89.33%;
    height: @base-size * 17.4;
    margin: auto;
    border-radius: @base-size * 0.85;
    background-color: @ft-white;
    padding: @base-size * 2 @base-size * 1.8 @base-size * 1.8 @base-size * 1.8;
    background: url(../../assets/images/ssg/anpay_card_ssg.png) no-repeat center;
    background-size: 100% 100%;
    box-sizing: inherit;
    .card-account {
      display: flex;
      justify-content: flex-start;
      flex-flow: row nowrap;
      align-items: center;
      .account-info {
        color: @info-name-color-ssg;
        overflow: hidden;
        .info-name {
          padding-bottom: @base-size * 0.4;
          font-size: @base-size * 1.9;
          line-height: 1em;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        .info-number {
          font-size: @base-size * 1.4;
          color: @info-number-color-ssg;
          line-height: 1em;
          padding-top: @base-size * 1.5;
        }
      }
    }
    .account-logo {
      width: @base-size * 3.5;
      height: @base-size * 3.5;
      background: url(../../assets/images/sly_logo.png) no-repeat center;
      background-size: cover;
      margin: auto;
    }
  }
  &--input-field {
    color: #333;
    background-color: @ft-white;
    padding: @base-size*3.5 @base-size*2.5;
    transform: translateY(-@base-size * 9);
    .input-label {
      padding-bottom: @base-size;
    }
    .money-input {
      padding: 0;
      align-items: flex-end;
      border-bottom: @base-size*.1 solid @hr-border-color-ssg;
      margin-bottom: @base-size * 4;
      .input-label-class {
        font-size: @base-size * 1.4;
        width: auto;
        padding-right: @base-size;
      }
      .van-field__control {
        font-size: @base-size * 3;
        font-weight: bold;
      }
    }
  }
  .transition-drop-down {
    transition: all 0.3s ease-out;
    background-color: transparent;
    padding: @base-size*2.5;
    .btn {
      font-weight: bold;
      width: 100%;
      font-size: @base-size * 1.8;
      border-radius: @base-size * 2.25;
      border: @base-size*.1 solid @btn-color-ssg;
      box-shadow: 0 @base-size*.3 @base-size*.35 @base-size*.012 @btn-shadow-color-ssg;
    }
    .recharge {
      background-color: @btn-color-ssg;
    }
  }
  .drop-down {
    transform: translateY(-@base-size * 27.5);
  }
  &--keyboard {
    color: @ft-black;
    .van-key--blue,
    .van-key--blue.van-key--active {
      background-color: @btn-color-ssg;
      &:active {
        opacity: 0.9;
      }
      &::after {
        border-color: @btn-color-ssg;
      }
    }
  }
}
</style>
